<template>
  <div v-if="!isSubmitted" class="card-body">
    <div class="icon-section">
      <div class="icon-bg">
        <img src="../public/images/icon-star.svg" alt="rate us">
      </div>
    </div>
    <div class="text-section">
      <h1>How did we do?</h1>
      <p>Please let us know how we did with your support request. All feedback is appreciated
        to help us improve our offering!</p>
    </div>
    <div class="form-section">
      <form @submit.prevent="formSubmit">
          <button v-for="(button, index) in buttons" :key="index" @click="selectBtn(index)" :class="[{ rateBtnClicked: button.isClicked }, 'rateBtn']" type="button">{{ button.value }}</button>
        <button class="submit-btn" type="submit">Submit</button>
      </form>
    </div>
  </div>

  <div v-else class="card-body">
    <div class="thank-you-pic">
      <img src="../public/images/illustration-thank-you.svg" alt="">
    </div>
    <div class="thank-you-rating">
      <span>You selected {{ ratingValue }} out of 5</span>
    </div>
    <div class="thank-you-thanks">
      <h1>Thank You!</h1>
      <p>We appreciate you taking the time to give a rating. If you ever need more support, 
  donâ€™t hesitate to get in touch!</p>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      buttons: [{value:1, isClicked: false},{value:2, isClicked: false},{value:3, isClicked: false},{value:4, isClicked: false},{value:5, isClicked: false}],
      ratingValue: 0,
      isSubmitted: false
    }
  },
  methods: {
    formSubmit() {
      this.isSubmitted = true
    },
    selectBtn(id){
      id++
      for(let i = 0; i < this.buttons.length; i++){
        if(id == this.buttons[i].value){
          this.buttons[i].isClicked = true
          this.ratingValue = this.buttons[i].value
        } else {
          this.buttons[i].isClicked = false
        }
      }
      
    }
  }
}
</script>

